import { Item } from "./ForceDirectedGraph/types.d"
import Point2D from "./ForceDirectedGraph/Point2D"

export default (item: Item): Point2D => {
  let radius: number, theta: number, lastPostId: string

  if (lastPostId != item.postId) {
    lastPostId = item.postId

    const tagString: string = item.tags.sort().join("") || "uncategorized"

    let codeSum: number = 0
    for (let i = tagString.length - 1; i >= 0; i--) {
      codeSum += tagString.charCodeAt(i)
    }

    theta = (codeSum % 628) / 100 + 0.05 * Math.random()

    radius = 0.1 + 0.5 * Math.random()
  }

  radius += 0.01

  return new Point2D(radius * Math.cos(theta), radius * Math.sin(theta))
}
