import { Item } from "./ForceDirectedGraph/types.d"
import { intersection, toUpper } from "ramda"

export default (sourceItem: Item, targetItem: Item): object => {
  const sameTags = intersection(
    sourceItem.tags.map(toUpper),
    targetItem.tags.map(toUpper)
  )

  let similarity = sameTags.length >= 1 ? 5 + 5 * Math.log(sameTags.length) : -5

  if (sourceItem.postId && sourceItem.postId === targetItem.postId) {
    similarity += 20
  }

  return { similarity }
}
