import { Item, Relationship } from "./ForceDirectedGraph/types.d"
import Point2D from "./ForceDirectedGraph/Point2D"
import { distance2D } from "./ForceDirectedGraph/distance"

export default (
  targetItem: Item,
  sourceItem: Item,
  areaSize: Point2D,
  relationship: Relationship
): Point2D => {
  const { magnitude: scalarDistance, direction } = distance2D(
    targetItem.position,
    sourceItem.position,
    areaSize
  )
  const { similarity } = relationship

  const closeForce = -1.5 / (scalarDistance / 30)

  const strength =
    similarity > 0
      ? similarForce(scalarDistance, closeForce, similarity)
      : dissimilarForce(scalarDistance, closeForce, similarity)

  return new Point2D(strength * direction.x, strength * direction.y)
}

const dissimilarForce = (
  scalarDistance: number,
  closeForce: number,
  similarity: number
) =>
  scalarDistance < 0.9
    ? closeForce + (10 * similarity) / (100 * (scalarDistance + 1))
    : 0

const similarForce = (
  scalarDistance: number,
  closeForce: number,
  similarity: number
) => closeForce + (800 * similarity) / (40 * (scalarDistance + 1))
