import React, { Component } from "react"
import blogClient from "../../hexagons/clients/blog"
import {
  buildPositionMatrix,
  assignItemsToNearestAddress,
} from "../../lib/hexGrid"
import ForceDirectedGraph from "../../lib/ForceDirectedGraph/ForceDirectedGraph"
import Point2D from "../../lib/ForceDirectedGraph/Point2D"
import PlayPauseButton from "./PlayPauseButton"
import forceFunction from "../../lib/forceFunction"
import getInitialPlacement from "../../lib/getInitialPlacement"
import getSimilarityRelationship from "../../lib/getSimilarityRelationship"

const THETA_POSITIONS = 40
const PHI_POSITIONS = 30

const PALETTE = [
  "#a00",
  "#0a0",
  "#00a",
  "#990",
  "#da0",
  "#099",
  "#909",
  "#555",
  "#f3f",
]
const TWO_PI = 2 * Math.PI

class ForceDirectedSnappedPosts extends Component {
  areaSize = new Point2D(TWO_PI, TWO_PI)
  ongoing = false
  onToggleOngoing = bool => {
    this.ongoing = bool
    if (this.ongoing) {
      this.step()
    }
  }
  groupsOngoing = true

  componentDidMount() {
    this.hexagonCoordMatrix = buildPositionMatrix(
      THETA_POSITIONS,
      PHI_POSITIONS
    )
    const scale = 500 / TWO_PI
    this.hexagonCoordMatrix.forEach((rowArray, row) => {
      rowArray.forEach((coords, col) => {
        this.backgroundCtx.fillRect(
          scale * coords.theta,
          scale * coords.phi,
          1,
          1
        )
      })
    })

    blogClient.Post.list().then(response => {
      const items = response.data().items
      this.setup(items)
    })
  }

  randomize = () => {
    this.fdg.initializePositions(this.items)
    this.draw()
  }

  findGroupRepresentatives = items => {
    const representatives = {}

    items.forEach(item => {
      const tags = item.tags.map(tag => tag.toLowerCase()).sort()
      if (!representatives[tags.join(",")]) {
        representatives[tags.join(",")] = item
      }
    })

    return Object.values(representatives)
  }

  setupGroupsItems = () => {
    this.representatives = this.findGroupRepresentatives(this.items)
    this.tagGroupFdg = new ForceDirectedGraph(this.areaSize, 0.00001)
    this.tagGroupFdg.forceOnItemFromSourceItem = forceFunction
    this.tagGroupFdg.getInitialPlacement = getInitialPlacement
    this.tagGroupFdg.initializePositions(this.representatives)
    this.tagGroupFdg.getItemsRelationship = this.getSimilarityRelationship
    this.tagGroupFdg.prepareRelationshipMatrix()

    this.drawGroups()
    this.stepGroups()
  }

  setup = items => {
    this.items = items
    this.setupGroupsItems(items)

    this.fdg = new ForceDirectedGraph(this.areaSize, 0.00001)
    this.fdg.getItemsRelationship = getSimilarityRelationship
    this.fdg.getInitialPlacement = getInitialPlacement
    //    this.randomize()

    this.fdg.forceOnItemFromSourceItem = forceFunction
    //    this.fdg.prepareRelationshipMatrix()

    //    this.draw()
  }

  BUMP_VALUE = 0.6

  bump = () => {
    this.fdg.bump(this.BUMP_VALUE)
    this.draw()
  }

  step = () => {
    this.fdg.step()

    this.draw()

    if (this.ongoing) {
      setTimeout(this.step, 1)
    }
  }

  stepGroups = () => {
    this.tagGroupFdg.step()

    this.drawGroups()

    if (this.groupsOngoing) {
      setTimeout(this.stepGroups, 1)
    }
  }

  drawGroups = () => {
    this.tagGroupCtx.clearRect(0, 0, 500, 500)

    this.tagGroupFdg.placedItems.forEach(item => {
      const pos = new Point2D(item.position.x, item.position.y)
      const scale = 500 / TWO_PI
      pos.scale(scale)

      let step = TWO_PI / item.tags.length

      item.tags.forEach((tag, i) => {
        this.tagGroupCtx.beginPath()
        this.tagGroupCtx.moveTo(pos.x, pos.y)
        this.tagGroupCtx.arc(pos.x, pos.y, 3, i * step, (i + 1) * step)
        this.tagGroupCtx.fillStyle = PALETTE[tag.charCodeAt(0) % PALETTE.length]
        this.tagGroupCtx.fill()
      })
    })
  }

  draw = () => {
    this.ctx.clearRect(0, 0, 500, 500)

    this.fdg.placedItems.forEach(item => {
      const pos = new Point2D(item.position.x, item.position.y)
      const scale = 500 / TWO_PI
      pos.scale(scale)

      let step = TWO_PI / item.tags.length

      item.tags.forEach((tag, i) => {
        this.ctx.beginPath()
        this.ctx.moveTo(pos.x, pos.y)
        this.ctx.arc(pos.x, pos.y, 3, i * step, (i + 1) * step)
        this.ctx.fillStyle = PALETTE[tag.charCodeAt(0) % PALETTE.length]
        this.ctx.fill()
      })
    })
  }

  snapItems = () => {
    assignItemsToNearestAddress(
      this.fdg.placedItems,
      this.areaSize,
      this.hexagonCoordMatrix,
      THETA_POSITIONS,
      PHI_POSITIONS
    )

    this.draw()
  }

  render() {
    return (
      <>
        <div style={{ height: 500 }}>
          <canvas
            style={{ position: "absolute" }}
            width="500"
            height="500"
            ref={ref => (this.backgroundCtx = ref.getContext("2d"))}
          />
          <canvas
            style={{ position: "absolute" }}
            width="500"
            height="500"
            ref={ref => (this.ctx = ref.getContext("2d"))}
          />
          <canvas
            style={{ position: "absolute" }}
            width="500"
            height="500"
            ref={ref => (this.tagGroupCtx = ref.getContext("2d"))}
          />
        </div>
        <button onClick={this.randomize}>randomize</button>
        &nbsp;
        <button onClick={this.bump}>bump</button>
        &nbsp;
        <PlayPauseButton onToggle={this.onToggleOngoing} />
        &nbsp;
        <button onClick={this.snapItems}>assign</button>
      </>
    )
  }
}

export default ForceDirectedSnappedPosts
